export class Level{
    private levelIndex:number;
    private levelMessage:string;
constructor(){
    this.levelIndex=null;
    this.levelMessage=null;
}
    get LvlIndex(){
        return this.levelIndex;
    }

    get LvlMsg(){
        return this.levelMessage;
    }

    set LvlIndex(val){
  this.levelIndex=val;
    }

    set LvlMsg(val){
        this.levelMessage=val;
    }
   
    
}
export class DelivLevel{
    private levelId:string;
    private maxLevel:number;
    private level:Array<Level>;
   constructor(){
       this.levelId=null;
       this.maxLevel=null;
       this.level=[];
   }
get LvlId(){
    return this.levelId;
}
set LvlId(val){
    this.levelId=val;
}

    get MaxLvl(){
        return this.maxLevel;
    }

    get Level(){
        return this.level;
    }

    set MaxLvl(val:number){
      this.maxLevel=val;
    }

    set Level(val){
this.level=val;
    }

    
}

