import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class orderGetService{
    private url=url.getAdminCustomerData;
private setUrl=url.setOrderDelLevel;
private getStandardDeliverydata=url.getSTDeliveryData;
private getExpressDeliverydata=url.getEXPDeliveryData;
private addressurl=url.getAddressOfOrder;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
            err=comments.networkError;
        }
  
          return throwError(
      err);
         };


    getCustomerData(customerId):Observable<any>{
        return this.http.post(this.url,customerId).pipe(catchError(this.handleError));
    }

    getStandardDeliveryData(deliveryId):Observable<any>{
        return this.http.post(this.getStandardDeliverydata,deliveryId).pipe(catchError(this.handleError));
    }

    getExpressDeliveryData(deliveryId):Observable<any>{
        return this.http.post(this.getExpressDeliverydata,deliveryId).pipe(catchError(this.handleError));
    }
    changeLevel(leveldata):Observable<any>{
        return this.http.post(this.setUrl,leveldata).pipe(catchError(this.handleError));
    }
     
    getSingleOrder(idobj):Observable<any>{
        return this.http.post(this.addressurl,idobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
     


}