import {Injectable} from '@angular/core'
import { url } from '../../../../globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from '../../../../globalmodel/comments';
import { httpOptions } from 'src/app/globalmodel/headerDetails';

@Injectable()

export class VendorService{

private searchurl=url.vsearchByPin;
private allocateUrl=url.allocateUrl;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

searchByPin(pin,delId):Observable<any>{
    let url = this.searchurl+"?pin="+pin+'&'+'delId='+delId;
  return this.http.get(url).pipe(catchError(this.handleError));
  
}

allocate(vendor):Observable<any>{
return this.http.post(this.allocateUrl,vendor,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

}

}