import { Component, OnInit, OnDestroy } from '@angular/core';
import { TimeSlot } from 'src/app/globalmodel/timeSlot';
import { timeSlotCrud } from './services/timeslotCrud';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
@Component({
  selector: 'app-time-slot',
  templateUrl: './time-slot.component.html',
  styleUrls: ['./time-slot.component.css'],
  providers:[timeSlotCrud]
})
export class TimeSlotComponent implements OnInit,OnDestroy {
public timeSlots:Array<TimeSlot>=[];
public destroyed= new Subject();
  constructor(private timeSlotService:timeSlotCrud,private router:Router) { }

  backendCall(){
    this.timeSlots=[];
this.timeSlotService.getTimeSlot().subscribe(data=>{
  if(data.status=='S'){
   // console.log(data);
    this.timeSlots=data.timeSlots;
  }else{
    console.log('Unknown Error');
  }
},err=>{
  this.timeSlots=[];
})
  }

  ngOnInit() {
  
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.backendCall();
    });
    this.backendCall();


  }

  addNewTS(){
    let newtimeSlot = new TimeSlot();
    this.timeSlots.push(newtimeSlot);
  }

  ngOnDestroy(){
    this.destroyed.next();
    this.destroyed.complete();
  }
}
