import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {TimeSlot} from '../../../globalmodel/timeSlot';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { timeSlotCrud } from '../time-slot/services/timeslotCrud';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';
@Component({
  selector: 'ind-time-slot',
  templateUrl: './ind-time-slot.component.html',
  styleUrls: ['./ind-time-slot.component.css'],
  providers:[timeSlotCrud]
})
export class IndTimeSlotComponent implements OnInit {
@Input() timeSlot:TimeSlot=null;
private form:FormGroup;
  constructor(private fb:FormBuilder,private timeSlotCrud:timeSlotCrud,private router:Router,
    private snackBar:MatSnackBar){ 

  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }
  

  createForm(){
    this.form= this.fb.group({
      _id:[this.timeSlot._id,[Validators.required]],
      starthour:[this.timeSlot.starthour,[Validators.required]],
      endhour:[this.timeSlot.endhour,[Validators.required]],
      startmin:[this.timeSlot.startmin,[Validators.required]],
      endmin:[this.timeSlot.endmin,[Validators.required]],
      startampm:[this.timeSlot.startampm,[Validators.required]],
      endampm:[this.timeSlot.endampm,[Validators.required]],
      isAvail:[this.timeSlot.isAvail,[Validators.required]]
    })
  }

  ngOnInit(){
  this.createForm();
  }

  setStTime(form){
    form.ststarthour=form.starthour;
      form.stendhour=form.endhour;           //set the time in 24 hour for comparison in backend

    if(form.startampm=='PM' && form.starthour!=12){
      form.ststarthour=form.starthour+12;
    }if(form.endampm=='PM' && form.endhour!=12){
      form.stendhour=form.endhour+12;
    }if(form.startampm=='AM' && form.starthour==12){
      form.ststarthour=24;
    }if(form.endampm=='AM' && form.endhour==12){
      form.stendhour=24;
    }
      
  }
  submit(form:NgForm){
    this.setStTime(form);
  //  console.log(form);
   this.timeSlotCrud.updateOrAddTimeSlot({tsobj:form}).subscribe(data=>{
     if(data.status=='S'){
    if(data.isAdded==true || data.isPushed==true){
      this.router.navigate(['/admin/timeSlotCrud']);
      
    }
     }
   },err=>{
  if(err.message!=null){
    this.openSnackBar(err.message,'');
  }else{
    this.openSnackBar(err,'');
  }
   });
   
  }

  deleteThis(){
   // console.log(this.form.value._id);
    this.timeSlotCrud.deleteTimeSlot({tsobj_id:this.form.value._id}).subscribe(data=>{
   if(data.status=='S'){
    if(data.isDeleted==true){
      this.router.navigate(['/admin/timeSlotCrud']);
    }
     }
   },err=>{
    if(err.message!=null){
      this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar(err,'');
    }
   });
   
  }
}
