import { Component, OnInit } from '@angular/core';
import { orderStream } from './services/orderStream';
import { OrderModel } from 'src/app/globalmodel/sendorder.model';
import {time} from '../../../globalmodel/timeInterval';
import {DateAdapter, MatSnackBar} from '@angular/material';
import{DateFormat}from '../../../globalmodel/dateFormat';
import { delOptDataService } from '../deliv-opt-page/services/delivStatusCrud';
@Component({
  selector: 'app-delivery-options',
  templateUrl: './order-options.component.html',
  styleUrls: ['./order-options.component.css'],
  providers:[orderStream,{provide: DateAdapter, useClass: DateFormat},delOptDataService]
})
export class OrderOptionsComponent implements OnInit {
public orders:Array<any>;
public levels:any=null;

public selectedOrder:any=null;
public startorderDate:Date;
public endorderDate:Date;
public intervalid:any;
  constructor(private delivlevelCrud:delOptDataService, private orderStream:orderStream,
    private dateAdapter:DateAdapter<Date>,private snackBar:MatSnackBar) {
    dateAdapter.setLocale('en-in');
   }
   openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  selectOrder(order){
this.selectedOrder=order;
  }

  getDelivLevels(){
this.delivlevelCrud.getDelivOptData().subscribe(data=>{
  if(data.status=='S'){
    console.log(data)
  this.levels=data.dellevels;
    }
  },(err)=>{
   //console.log(err);
   if(err.message!=null){
    if(err.status=='E'|| err.status=='EMP'){
   this.openSnackBar(err.message,'');
    }}else{
      this.openSnackBar(err,'');
    }


})
  }
  

  backendGetOrders(){
    this.orderStream.getOrder({beginDate:this.startorderDate,endDate:this.endorderDate}).subscribe(data=>{
      if(data.status=='S'){
        this.orders=data.orders;
      }
      },err=>{
        if(err.message!=null){
        this.openSnackBar(err.message,'');}
        else{
          this.openSnackBar(err,'');
        }
      })
    
  }
  ngOnInit() {
    this.getDelivLevels();
    let date= new Date();
 this.startorderDate=new Date(date.getFullYear(),date.getMonth(),date.getDate());
this.endorderDate = new Date(Date.now());
this.backendGetOrders();

this.intervalid=setInterval(()=>{
  this.backendGetOrders();

},time.orderTimeInterval)


    
  }
  ngOnDestroy() {
    if (this.intervalid) {
      clearInterval(this.intervalid);
    }
  }
}
