import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AdminService } from '../moduleServices/adminService';
import { MatSnackBar } from '@angular/material';


@Component({
    selector:'admindashboard',
    templateUrl:'./dashboard.component.html',
    styleUrls:['./dashboard.component.css'],
    providers:[AdminService]

})

export class DashboardComponent implements OnInit{
    public rights:Array<any>=[];
    public superAdmin=false;
constructor(private router:Router,private adminCrud:AdminService,private snackBar:MatSnackBar){

}
openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
}


getRoleAndRights(){
this.adminCrud.getAdminRights().subscribe(data=>{
    if(data.status=='S'){
     //   console.log(data.rights);
this.rights=data.rights;
this.superAdmin=data.isSuperAdmin;
    }
        else{
            this.openSnackBar('Unknown Error','');
                }
              },err=>{
                if(err.message){
            this.openSnackBar(err.message,'');
                }else{
            this.openSnackBar(err,'');
                }
              })
}

ngOnInit(){
    this.getRoleAndRights();
   
}
}