import { MatDialogRef, MatSnackBar } from '@angular/material';
import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA} from '@angular/material/dialog';
import { AllotedDeliveryService } from '../alloted-deliv/services/allotedDeliveryService';

@Component({
  selector: 'app-allocate-del-boy',
  templateUrl: './allocate-del-boy.component.html',
  styleUrls: ['./allocate-del-boy.component.css'],
  providers:[AllotedDeliveryService]
})
export class AllocateDelBoyComponent implements OnInit {
public searchedDelBoy:Array<any>=[];
public previousDelBoy=null;
public waitTillCall:boolean=true;
  constructor(@Inject(MAT_DIALOG_DATA) private data,private dialogRef:MatDialogRef<AllocateDelBoyComponent>,
  private allocateService:AllotedDeliveryService,private snackBar:MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
  }
  
  ngOnInit(){
   // console.log(this.data);
    this.getPreAllocatedDelBoy();
  }

getPreAllocatedDelBoy(){
  this.allocateService.getPreAllocatedDelBoy(this.data).subscribe(data=>{
    console.log(data);
    if(data.status=='S'){
   
      this.previousDelBoy=data.doc;

    }else{
this.waitTillCall=false;
    }
  },err=>{
this.waitTillCall=false;
  })
}

  autoAllocation(){
    this.allocateService.autoDelBoyAllocation(this.data).subscribe(data=>{
      if(data.status=='S' && data.isAllocated==true){
        this.dialogRef.close({change:true});
      }
    },err=>{
      if(err.message!=null){
       this.openSnackBar(err.message,'');
      }else{
       this.openSnackBar(err,'');
      }
    })
  }

  searchDelBoys(){
    this.allocateService.searchDelBoy(this.data.vendorId).subscribe(data=>{
      if(data.status=='S'){
        console.log(data);
        this.searchedDelBoy=data.delBoys;
      }
    },err=>{
      if(err.message!=null){
        this.openSnackBar(err.message,'');
      }else{
        this.openSnackBar(err,'');
      }
    })

  }

  manualAllocation(delboy){
    console.log(delboy);
    this.allocateService.manualDelBoyAllocation({delboy:delboy,deldata:this.data}).subscribe(data=>{
      if(data.status=='S'){
this.dialogRef.close({change:true});
      }
    },err=>{
      if(err.message!=null){
this.openSnackBar(err.message,'');
      }else{
this.openSnackBar(err,'');
      }
    })
  }

}
