import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { UnAllotedDeliveryService } from './services/unAcceptedService';
import { time } from 'src/app/globalmodel/timeInterval';

@Component({
  selector: 'un-accepted-del-orders',
  templateUrl: './un-accepted-del-orders.component.html',
  styleUrls: ['./un-accepted-del-orders.component.css'],
  providers:[UnAllotedDeliveryService]
})
export class UnAcceptedDelOrdersComponent implements OnInit,OnDestroy {
@Input() type=null;
@Output() emitDelData = new EventEmitter();
public errMessage:String=null;
public interval=null;
public deliveries:Array<any>=[];
  constructor(private unacceptedService:UnAllotedDeliveryService) {
    

   }
backendCall(){
 if(this.type!=null){
   this.unacceptedService.getAllotedDelivOrder(this.type).subscribe(data=>{
     if(data.status=='S'){
       this.errMessage=null;
this.deliveries=data.deliveries;
console.log(this.deliveries);
     }
   },err=>{
     this.deliveries=[];
     if(err.message!=null){
this.errMessage=err.message;
     }else{
       this.errMessage=err;
     }
   })
 }
}

sendThis(del){
del.products=del.vendorAvailProducts;
del.undeid=del._id;
console.log(del);
this.unacceptedService.getProducts({delobj:del}).subscribe(data=>{
  if(data.status=='S'){
    console.log(data);
    del.orderProducts=data.products[0].orderProducts;
    this.emitDelData.emit(del);
  }
},err=>{
  if(err.message!=null){
    alert(err.message);
  }else{
    alert(err);
  }
})
}


  ngOnInit() {
this.backendCall();
this.interval=setInterval(()=>{
  this.backendCall()
},time.orderTimeInterval)
  }

  ngOnDestroy(){
    if(this.interval){
    clearInterval(this.interval);
    }
  }
}
