
import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import {comments} from '../../../../globalmodel/comments';


@Injectable()
export class UnAllotedDeliveryService{
getunaceptedDelorder = url.getUnAcceptedDelOrder;
getproductsUrl = url.getProducts;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
    //   console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=0){
         err = error.error;
             }else{
                 err=comments.networkError;
             }
      
              return throwError(
          err);
             };

       
             getAllotedDelivOrder(type):Observable<any>{
         let url =this.getunaceptedDelorder+"?type="+type;
        return this.http.get(url).pipe(catchError(this.handleError));
 
    }
getProducts(delobj):Observable<any>{
    return this.http.post(this.getproductsUrl,delobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

}

}