import { Component, OnInit, OnDestroy } from '@angular/core';
import {GetVerifiedUsers} from './services/getVerifiedEmployees'
import { GetUnverifiedUsers} from "./services/getUnverifiedEmployees";
import { Verification } from './services/Verification';
import { MatSnackBar } from '@angular/material';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
@Component({
  selector: 'app-maker-checker',
  templateUrl: './maker-checker.component.html',
  styleUrls: ['./maker-checker.component.css'],
  providers:[GetUnverifiedUsers,Verification,GetVerifiedUsers]
})
export class MakerCheckerComponent implements OnInit ,OnDestroy{
public unverifiedUserData:any;
public verifiedUserData:any;
public rejectedUserData:any=[];
public destroyed= new Subject();
  constructor(private getUnverifiedUsers:GetUnverifiedUsers,
    private getVerifiedUser:GetVerifiedUsers,private verification:Verification,
    private snackBar:MatSnackBar,private router:Router) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }
  fetchData(){
    this.getUnverifiedUsers.getUsersData().subscribe(data=>{
     
      this.unverifiedUserData = data.users;
    },err=>{
      this.openSnackBar(err,'');
    })
 this.getVerifiedUser.getUsersData().subscribe(data=>{
   this.verifiedUserData=data.users;
 },err=>{
   this.openSnackBar(err,'');
 })

 this.getUnverifiedUsers.getRejectedUser().subscribe(data=>{
  this.rejectedUserData=data.users;
},err=>{
  this.openSnackBar(err,'');
})


  }

  ngOnInit() {

    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.fetchData();
    });
    this.fetchData();

  }

  verifyTheUser(user:any){
    console.log(user);
    this.verification.verifyUser(user).subscribe(data=>{
      if(data!=null){
        if(data.isVerified==true){
          user.isVerified=true;
 this.openSnackBar('User Is Verified','');
this.router.navigate([this.router.url]);
 
        }else{
          this.openSnackBar('Some Error Occured','');
        }
      }
    },err=>{
      this.openSnackBar(err,'');
    })
  }

  unverifyTheUser(user:any){
    console.log(user)
    this.verification.unverifyUser(user).subscribe(data=>{
      if(data!=null){
        if(data.isVerified==false){
 this.openSnackBar('User Is Unverified','');
 this.router.navigate([this.router.url]);
        }else{
          this.openSnackBar('Some Error Occured','');
        }
      }
    },err=>{
      this.openSnackBar(err,'');
    })
  
  }

  rejectTheUser(user){
    console.log(user);
    this.verification.rejectUser(user).subscribe(data=>{
      if(data!=null){
        if(data.isVerified==false){
 this.openSnackBar('User Is Rejected','');
 this.router.navigate([this.router.url]);
        }else{
          this.openSnackBar('Some Error Occured','');
        }
      }
    },err=>{
      this.openSnackBar(err,'');
    })
  }

    ngOnDestroy(){
      this.destroyed.next();
      this.destroyed.complete();

  }
}
