import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, NgForm, Validators } from '@angular/forms';
import { VoucherService } from './services/voucherServices';
import { MatSnackBar } from '@angular/material';
import { Subject } from 'rxjs';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { filter, takeUntil } from 'rxjs/operators';
@Component({
  selector: 'admin-voucher',
  templateUrl: './voucher.component.html',
  styleUrls: ['./voucher.component.css'],
  providers:[VoucherService]
})
export class VoucherComponent implements OnInit,OnDestroy {
public templateIdArray=[];
public destroyed = new Subject;
  public voucherDetailsArray=[];
  public voucherForm:FormGroup;
  constructor(private fb:FormBuilder,private router:Router,
    private voucherService:VoucherService,private snackBar:MatSnackBar) {

  }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }

  fetchData(){
    this.templateIdArray=[];
    this.voucherDetailsArray=[];
    this.voucherService.getVouchers().subscribe(data=>{
      // console.log(data);
      if(data.status=='S' && data.data.length!=0){
        let dataArray= data.data[0].allVouchers;
      this.templateIdArray=data.templates;
  
        this.voucherDetailsArray=[];
        if(dataArray.length!=0){
        for(let obj of dataArray){
           this.voucherDetailsArray.push(this.createVoucher(obj));
        }}else{
          this.templateIdArray=data.templates;
          this.voucherDetailsArray.push(this.createVoucher(null));
        }
        this.createForm();
  
      }else if(data.data.length==0){
        this.voucherDetailsArray.push(this.createVoucher(null));
        this.templateIdArray=data.templates;
        this.createForm();
      }
      },err=>{
        if(err.message!=null){
          this.openSnackBar(err.message,'');
        }else{
          this.openSnackBar(err,'');
        }
  
      })
  }
  ngOnInit() {  this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.fetchData();
    });
    this.fetchData();

  }

  
 
  createForm() {
    this.voucherForm=this.fb.group({
      voucherDetails:this.fb.array(this.voucherDetailsArray)
    })
}
get voucherForms() {
  return this.voucherForm.get('voucherDetails') as FormArray;
}

createVoucher(obj){
  let voucher=null;
  if(obj!=null){
  voucher= this.fb.group({
   
    voucherStatement:[obj.voucherStatement,[Validators.required]],
    expiryDate:[obj.expiryDate,[Validators.required]],
    isPercent:[obj.isPercent,Validators.required],
    voucherDiscount:[obj.voucherDiscount,[Validators.required]],
    templateId:[obj.templateId,[Validators.required]]
})
}else{
  voucher= this.fb.group({
   
    voucherStatement:[null,[Validators.required]],
    expiryDate:[null,[Validators.required]],
    isPercent:['true',Validators.required],
    voucherDiscount:[null,[Validators.required]],
    templateId:[null,[Validators.required]]
})

}
return voucher;

}
addVoucher(value) {
  this.voucherForms.push(this.createVoucher(value));
}
deleteVoucher(i) {
  this.voucherForms.removeAt(i);
}

onsubmit(form:NgForm){
 // console.log(form);
this.voucherService.pushVoucher(form).subscribe(data=>{
  if(data.status=='S'){
 this.router.navigate([this.router.url]);
  }
},err=>{
  if(err.message!=null){
    this.openSnackBar(err.message,'');
  }else{
    this.openSnackBar(err,'');
  }
})
}


ngOnDestroy(){
  this.destroyed.next();
  this.destroyed.complete();
}
}


