import { Component, OnInit, OnDestroy } from '@angular/core';
import { delOptDataService } from './services/delivStatusCrud';
import { DelivLevel, Level } from 'src/app/globalmodel/delivLevel';
import { MatSnackBar } from '@angular/material';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
@Component({
  selector: 'app-deliv-opt-page',
  templateUrl: './deliv-opt-page.component.html',
  styleUrls: ['./deliv-opt-page.component.css'],
  providers:[delOptDataService]
})
export class DelivOptPageComponent implements OnInit,OnDestroy {
public delivLevels:Array<DelivLevel>=[];
public destroyed= new Subject();
  constructor(private delopt:delOptDataService,private snackBar:MatSnackBar,private router:Router) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  fillobject(objectArray){
  for(let dellvlopt of objectArray){
    let pushopt = new DelivLevel();
    pushopt.LvlId=dellvlopt.levelId;
    pushopt.MaxLvl=dellvlopt.maxLevel;
    for(let levelobj of dellvlopt.levels){
    let lvlobj = new Level();
    lvlobj.LvlIndex=levelobj.levelIndex;
    lvlobj.LvlMsg= levelobj.levelMessage;
      pushopt.Level.push(lvlobj);
    }

    this.delivLevels.push(pushopt);
  }
  //console.log(this.delivLevels);
  }
  fetchData(){
    this.delivLevels=[];
    this.delopt.getDelivOptData().subscribe(data=>{
      if(data.status=='S'){
      this.fillobject(data.dellevels);
      }
    },(err)=>{
     //console.log(err);
     if(err.message!=null){
      if(err.status=='E'|| err.status=='EMP'){
     this.openSnackBar(err.message,'');
      }}else {
        this.openSnackBar(err,'');
      }
    })
  }
  ngOnInit() {
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.fetchData();
    });
    this.fetchData();

  }

addNew(){
  let level = new  Level()
 let deliv= new DelivLevel()
 deliv.Level.push(level);
  this.delivLevels.push(deliv);
  //console.log(this.delivLevels);
}

ngOnDestroy(){
  this.destroyed.next();
  this.destroyed.complete();
}

}
