import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { getErrorMessage } from 'src/app/globalServices/Validation';
import { MatSnackBar, MatDialog } from '@angular/material';
import { RoleService } from '../moduleServices/roleService';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { OverrideComponent } from '../Dialogs/override/override.component';
import { RightService } from '../moduleServices/rightsService';
@Component({
  selector: 'role',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.css'],
  providers:[RoleService,RightService]
})
export class RoleComponent implements OnInit {
  @Input() role;
  @Input() set SetRights(val){
   
this.Allrights= this.makeNewArray(val);
this.getMyRights();

  };


public myrights:Array<any>=[];
public error_messages;
public Allrights:Array<any>=[];
public roleForm:FormGroup;
  constructor(public errorMessage:getErrorMessage,
    private fb:FormBuilder,
    private router:Router,
    private dialog:MatDialog,
    private snackBar:MatSnackBar,
    private roleCrud:RoleService,
    private rightCrud:RightService) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });

  }

  makeNewArray(arr){
     return  arr.map(right=>{
         return {
           rightName:right.rightName,
           rightId:right.rightId,
           isAssign:false,
           _id:right._id
         }
       })
  }

  setAlreadyAssigned(){
    if(this.myrights.length!=0){
    this.Allrights.forEach(right=> {
      right.isAssign=false;
      this.myrights.forEach(myright=>{
        if(right.rightId==myright.rightId){
        myright.rightName=right.rightName;
          right.isAssign=true;
        }
      })

    });
  }
  }

getMyRights(){
  if(this.role.roleId!=null && this.role._id!=null){
    this.roleCrud.getRightofRole(this.role).subscribe(data=>{
      if(data.status=='S'){
          this.myrights=data.rights;
          console.log(this.myrights);
          this.setAlreadyAssigned();
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })
  }
  

}

refreshList(){
  this.getMyRights();
}

add(right){
this.roleCrud.addRoleRightMap({rightId:right.rightId,roleId:this.role.roleId}).subscribe(data=>{
  if(data.status=='S' && data.result.created==true){
    this.openSnackBar('Right Added','');
   this.refreshList();
  }else{
    this.openSnackBar('Unknown Error','')
   
  }
},err=>{
  if(err.message){
    this.openSnackBar(err.message,'')
          }else{
            this.openSnackBar(err,'');
          }
})


}

remove(right){
this.roleCrud.deleteRoleRightMap({rightId:right.rightId,roleId:this.role.roleId}).subscribe(data=>{
  if(data.status=='S'){
    this.openSnackBar('Right Removed','');
    this.refreshList();
  }else{
    this.openSnackBar('Unknown Error','')
  }
},err=>{
  if(err.message){
    this.openSnackBar(err.message,'')
          }else{
            this.openSnackBar(err,'');
          }
})

}

  ngOnInit() {
   
    this.error_messages=this.errorMessage.geterror();
    this.roleForm=this.fb.group({
      roleName:[this.role.roleName,[Validators.required]],
      roleDesc:[this.role.roleDesc,[Validators.required]],
      active:[this.role.active,[Validators.required]]
    })

  }

  create(form){
  
    this.roleCrud.createRole(form).subscribe(data=>{
      if(data.status=='S'){
        if(data.result.isCreated==true){
          this.router.navigate([this.router.url]);
        }else if(data.result.override==true){
          let dialogRef=this.dialog.open(OverrideComponent,{
            width:'25%'
          })

          dialogRef.afterClosed().subscribe(result=>{
            if(result==true){
              this.update(form,data.result.doc);
            }
          })
        }
       
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })

  }

  update(form,role){

 form._id=role._id;
 form.roleId=role.roleId;
    this.roleCrud.updateRole(form).subscribe(data=>{
      if(data.status=='S' && data.result.isUpdated==true){
        this.router.navigate([this.router.url]);
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
this.openSnackBar(err.message,'')
      }else{
        this.openSnackBar(err,'');
      }
    })

  }

  delete(){

    this.roleCrud.deleteRole(this.role).subscribe(data=>{
      if(data.status=='S' && data.result.isDeleted==true){
        this.router.navigate([this.router.url]);
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })

  }


}
