import { Component, OnInit, Input } from '@angular/core';
import { AdminService } from '../moduleServices/adminService';
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { getErrorMessage } from 'src/app/globalServices/Validation';

@Component({
  selector: 'admin-role',
  templateUrl: './admin-role.component.html',
  styleUrls: ['./admin-role.component.css'],
  providers:[AdminService]
})
export class AdminRoleComponent implements OnInit {
@Input() admin;
@Input() set SetRoles(val){
this.roles=val;
this.getAdminRole();
}
public roles:Array<any>=null;
  public adminForm:FormGroup;
public getMyRoleAdminMap:string;
public error_messages;
openSnackBar(message: string, action: string) {
  this.snackBar.open(message, action, {
    duration: 1000,
  });
}


  constructor(private adminCrud:AdminService,private fb:FormBuilder,
    private snackBar:MatSnackBar,private router:Router,private errorMessage:getErrorMessage) { 
  
  }

  getAdminRole(){
    if(this.admin._id!=null && this.admin.id!=null){

  
    this.adminCrud.getAdminRoleMap(this.admin).subscribe(data=>{
      if(data.status=='S'){
        if(data.result==null){
          this.getMyRoleAdminMap=data.result;
        }else{
          this.getMyRoleAdminMap=data.result.roleId;
        }
    
      }
    },err=>{
      if(err.message){
this.openSnackBar(err.message,'');
      }else{
this.openSnackBar(err,'');
      }
    })
  }
  }

  create(form){
  
    this.adminCrud.createAdmin(form).subscribe(data=>{
      if(data.status=='S'){
        if(data.result.isCreated==true){
          this.router.navigate([this.router.url]);
        }
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })

  }

  update(form,admin){

 form._id=admin._id;
 form.id=admin.id;
    this.adminCrud.updateAdmin(form).subscribe(data=>{
      if(data.status=='S' && data.result.isUpdated==true){
        this.router.navigate([this.router.url]);
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
this.openSnackBar(err.message,'')
      }else{
        this.openSnackBar(err,'');
      }
    })

  }

  delete(){

    this.adminCrud.deleteAdmin(this.admin).subscribe(data=>{
      if(data.status=='S' && data.result.isDeleted==true){
        this.router.navigate([this.router.url]);
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })

  }


  ngOnInit() {
    this.error_messages=this.errorMessage.geterror();
this.adminForm=this.fb.group({
  name:[ this.admin.name,[Validators.required]],
  password:[this.admin.password,[Validators.required]],
  activestatus:[this.admin.activestatus,[Validators.required]]
})
  
}

changeRole(){
  if(this.getMyRoleAdminMap!=null){
    this.adminCrud.changeAdminRoleMap({id:this.admin.id,roleId:this.getMyRoleAdminMap}).subscribe(data=>{
      if(data.status=='S'){
        this.openSnackBar('Change done','');
        this.router.navigate([this.router.url]);
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })
  }
  }
  

}
