import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class delOptDataService{
    private geturl=url.getDelivLevels
private addUrl= url.addDelivLevel;
private deleteUrl=url.deleteDelivLevel;
private editUrl= url.editDelivLevel;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
            err=comments.networkError;
        }
  
          return throwError(
      err);
         };


    getDelivOptData():Observable<any>{
        return this.http.get(this.geturl).pipe(catchError(this.handleError));
    }

    addDelivOptData(delivobj):Observable<any>{
        return this.http.post(this.addUrl,delivobj).pipe(catchError(this.handleError));
    }
   editDelivOptData(delivobj):Observable<any>{
    return this.http.post(this.editUrl,delivobj).pipe(catchError(this.handleError));
}
remove(delivobj):Observable<any>{
    return this.http.post(this.deleteUrl,delivobj).pipe(catchError(this.handleError));
}


}