
import { Component, Inject, OnInit } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material'
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';

@Component({
  selector: 'app-unverifyemail',
  templateUrl: './unverifyemail.component.html',
  styleUrls: ['./unverifyemail.component.css']
})
export class UnverifyemailComponent implements OnInit {
public emailobj:FormGroup;
  constructor(private fb:FormBuilder,@Inject(MAT_DIALOG_DATA) public object: any,
  public dialogRef: MatDialogRef<UnverifyemailComponent>,) { }

  ngOnInit() {
    this.emailobj=this.fb.group({
      to:[this.object.user.email,[Validators.required]],
      subject:[null,[Validators.required]],
      reason:[null,[Validators.required]]
    })
  }

  send(form:NgForm){
    this.dialogRef.close({confirm:true,data:form})
    }


}
