import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { AllotedDeliveryService } from '../alloted-deliv/services/allotedDeliveryService';
import { time } from 'src/app/globalmodel/timeInterval';
import * as moment from 'moment';
import { MatDialog, MatSnackBar } from '@angular/material';
import { AllocateDelBoyComponent } from '../allocate-del-boy/allocate-del-boy.component';
@Component({
  selector: 'alloted-del-data',
  templateUrl: './alloted-del-data.component.html',
  styleUrls: ['./alloted-del-data.component.css'],
  providers:[AllotedDeliveryService]
})
export class AllotedDelDataComponent implements OnInit,OnDestroy {
@Input() delivorder=null;
@Output() emitChange = new EventEmitter();
private startdate;
private isPushed=false;
private maxTimeLimit=5;
private minute:number=null;
private hours:number=null;
private date:number;
private seconds:number=null;
  constructor(private allotService:AllotedDeliveryService,private dialog:MatDialog,private snackBar:MatSnackBar) { }

parseJsonDate(jsonDateString){
    return new Date(moment(jsonDateString).format());
}

openSnackBar(message: string, action: string) {
  this.snackBar.open(message, action, {
    duration: 1000,
  });
}

allocateDeliveryBoy(){
let dialogRef=this.dialog.open(AllocateDelBoyComponent,{
  width:'50%',
  height:'auto',
  data:this.delivorder
})
dialogRef.afterClosed().subscribe(data=>{
  if(data!= null && data.change==true){
    this.emitChange.emit({change:true})
  }
})
}


backendCall(){
 // console.log(this.startdate);
  let currentDate = new Date();
// console.log(this.delivorder);
  this.minute=currentDate.getMinutes()-this.startdate.getMinutes();
  this.seconds=currentDate.getSeconds()-this.startdate.getSeconds();
  this.hours=currentDate.getHours()-this.startdate.getHours();
  this.date=currentDate.getDate()-this.startdate.getDate();
  if(this.minute>this.maxTimeLimit || this.hours>0 || this.date>0){
    console.log('here check');
 // console.log(this.minute,this.hours,this.date);
 if(this.isPushed==false){
  this.allotService.shiftToReturnedSlot({delobj:this.delivorder}).subscribe(data=>{
    if(data.status=='S' && data.isPushed==true){
      this.isPushed=true;
    this.emitChange.emit({change:true})
    }
  },err=>{
    if(err.isAccepted==true){ 
      this.isPushed=true;   
     this.emitChange.emit({change:true})
    }else{
      if(err.message!=null){
      this.openSnackBar(err.message,'');
      }else{
        this.openSnackBar(err,'');
      }
    }
  })
 }
   
  }
}

  ngOnInit() {
    if(this.delivorder!=null){
      if(this.delivorder.vendorsellDelProducts.length==0){
   //  console.log(this.delivorder.allotStatus.vendorAllotStatus.allotedDate)
          this.startdate = this.parseJsonDate(this.delivorder.allotStatus.vendorAllotStatus.allotedDate);
        //  console.log(this.startdate);
          this.backendCall();
      }
    }

  }

ngOnDestroy(){
 
}

}
