import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA} from '@angular/material/dialog';
import { VendorService } from './services/vendorService';
import { MatDialog, MatDialogRef, MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-nearest-vendor',
  templateUrl: './nearest-vendor.component.html',
  styleUrls: ['./nearest-vendor.component.css'],
  providers:[VendorService]
})
export class NearestVendorComponent implements OnInit {
public searchvendorpin;
public vendorList:Array<any>;
  constructor(@Inject(MAT_DIALOG_DATA) private data, private vservice:VendorService,
  private dialogRef:MatDialogRef<NearestVendorComponent>,private snackBar:MatSnackBar ) { }

  ngOnInit() {
console.log(this.data);
  
  }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  searchVendor(){

    if(this.searchvendorpin.length==6){
this.vservice.searchByPin(this.searchvendorpin,this.data.deliveryId).subscribe(data=>{
  if(data.status=='S'){
    console.log(data);
    this.vendorList=data.vendors;
  }
},err=>{
  if(err.message!=null){
    alert(err.message);
  }else{
    alert(err);
  }
})
    }
  }

  allocateThisVendor(vendor){
let ids =[];
  this.data.orderProducts.forEach(element => {
    ids.push(element.opId);
  });

  
    let obj ={
      _id:this.data._id,
      undeid:this.data.undeid,
      vendorstatus:true,
      vendorId:vendor.id,
      prodIds:ids,
      deliveryId:this.data.deliveryId,
      type:this.data.type,
      orderId:this.data.orderId
    }
   // console.log(obj);
this.vservice.allocate({delobj:obj}).subscribe(data=>{
  if(data.status=='S' && data.isPushed==true){
this.close(data.isPushed);
  }else{
   this.openSnackBar('Unknown Error','');
  }
},err=>{
  this.vendorList=[];
if(err.message!=null){
  this.openSnackBar(err.message,'');
}else{
  this.openSnackBar(err,'');
}
})
  }

close(val){
  this.dialogRef.close(val);
}

}
