import { Component, OnInit, Input, OnDestroy, Output, EventEmitter } from '@angular/core';
import { AllotedDeliveryService } from './services/allotedDeliveryService';
import {time} from '../../../globalmodel/timeInterval';
@Component({
  selector: 'alloted-deliv',
  templateUrl: './alloted-deliv.component.html',
  styleUrls: ['./alloted-deliv.component.css'],
  providers:[AllotedDeliveryService]
})
export class AllotedDelivComponent implements OnInit,OnDestroy {
@Input() type;
@Output() emitChange = new EventEmitter();
public allotedDeliveries:Array<any>=[];
public errMessage=null;
public interval=null;
  constructor(private allotedService:AllotedDeliveryService) { }

  ngOnInit() {
    this.backendCall();
   this.interval= setInterval(()=>{
 this.backendCall();
    },time.deliverytime)

  }
backendCall(){
  console.log(this.type);
  this.allotedService.getAllotedDelivOrder({type:this.type}).subscribe(data=>{
    if(data.status=='S'){
      this.errMessage=null;
      this.allotedDeliveries=data.deliveries;
    }
  },err=>{
    this.allotedDeliveries=[];
    if(err.message!=null){
this.errMessage=err.message;
    }else{
      this.errMessage=err;
    }
  })
}

captureChange(data){
 if(data.change==true){
   this.emitChange.emit(data.change);
 }
}
ngOnDestroy(){
  if(this.interval){
    clearInterval(this.interval);
  }
}

}
