import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { RoleService } from '../moduleServices/roleService';
import { MatSnackBar } from '@angular/material';
import { RoleModel } from '../modulemodels/role.model';
import { RightService } from '../moduleServices/rightsService';
@Component({
  selector: 'app-role-crud',
  templateUrl: './role-crud.component.html',
  styleUrls: ['./role-crud.component.css'],
  providers:[RoleService,RightService]
})
export class RoleCrudComponent implements OnInit ,OnDestroy{
public destroyed= new Subject();
public roles:Array<any>=[];
public rights:Array<any>=[];
  constructor(private router:Router,
    private roleService:RoleService,
    private snackBar:MatSnackBar,
    private rightCrud:RightService) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });

  }
  getRoles(){
    this.roleService.getAllRoles().subscribe(data=>{
      if(data.status=='S'){
this.roles=data.roles;
      }else{
this.openSnackBar('Unknown Error','');
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'');
      }else{
        this.openSnackBar(err,'');
      }
    })

  }

 
getRights(){
this.rightCrud.getAllRights().subscribe(data=>{
if(data.status=='S'){
this.rights=data.rights;
//console.log(this.rights);

}
},err=>{
  if(err.message){
    this.openSnackBar(err.message,'');
  }else{
    this.openSnackBar(err,'');
  }
})
}

  ngOnInit() {
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.getRoles();
      this.getRights();
    });
    this.getRoles();
    this.getRights();

  
  }



  createNewRole(){
    let obj:RoleModel= new RoleModel();
    obj._id=null;
    obj.active=true;
    obj.roleDesc=null;
    obj.roleId=null;
    obj.roleName=null;
  this.roles.push(obj);
  }

  ngOnDestroy(){
    this.destroyed.next();
    this.destroyed.complete();
  }

}
