import { Component, OnInit, OnDestroy } from '@angular/core';
import {getProductArray} from '../../../globalServices/filterProductList'
import {Ads} from '../../../globalmodel/ad/ads.modal'
import {AdList} from './services/adList';
import { MatSnackBar } from '@angular/material';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
@Component({
  selector: 'app-adspage',
  templateUrl: './adspage.component.html',
  styleUrls: ['./adspage.component.css'],
  providers:[AdList]
})
export class AdspageComponent implements OnInit,OnDestroy {
public adArray:Ads[]=[];
public currentopened:number=0;
public productArray=[];
public categoryNameArray=[];
public bankArray=[];
public completeList=[];
public destroyed = new Subject<any>();
  constructor(private getAdList:AdList,private snackBar:MatSnackBar,private router:Router) { }


  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  fetchData(){
    this.categoryNameArray=[];
    this.productArray=[];
    this.bankArray=[];
    this.adArray=[];
    this.getAdList.getListAds().subscribe(data=>{
      //console.log(data);
      if(data!=null && Array.isArray(data)==true && data.length!=0){
        data.forEach(list => {
          if(list.adalready!=null && list.adalready.length!=0){
            this.adArray= list.adalready;
            this.sendcategoryName(this.adArray);
           }
          else if(list.productAd!=null && list.productAd.length!=0){
           this.completeList.push({'Product':list.productAd});
         //  console.log(prodlist);
         }
         else if(list.bankAd!=null && list.bankAd.length!=0){
          
          this.completeList.push({'Bank':list.bankAd});
          
        }
        });
      }else{
       this.openSnackBar('No Product Data','');
      }  
    },err=>{
    this.openSnackBar(err,'');
    })
  }
  ngOnInit(){
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.fetchData();
    });
    this.fetchData();

  }


  sendcategoryName(array){
  array.forEach(ad => {
    this.categoryNameArray.push(ad.category);
    
  });
  }

  openStatus(i){
    this.currentopened=i;
   // console.log(this.currentopened);
  }
  returnnewAdd(){
    let ad = new Ads();
   ad.categoryName='';
   ad.layoutvalue='';
   ad.noofads='';
   ad.subadsarray=null;
   return ad;

  }
  addAd(){
    this.adArray.push(this.returnnewAdd());
  }
deleteAd(object){
  for(let ad of this.adArray){
    if(ad.categoryName==object.category){
      this.adArray.splice(object.index,1);
    }
  }
  
}

ngOnDestroy(){
  this.destroyed.next();
  this.destroyed.complete();
}

}
