import {Injectable, NgZone} from '@angular/core';
import { url } from '../../../../globalmodel/url';
import { Observable ,throwError} from 'rxjs';
import {HttpClient,HttpErrorResponse} from '@angular/common/http'
import { catchError } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { comments } from 'src/app/globalmodel/comments';


@Injectable()

export class timeSlotCrud{
private getTSurl= url.getAllTimeSlots;
private updateOrAddurl=url.updateTimeSlot;
private deleteUrl = url.deleteTimeSlot;
constructor(private http:HttpClient){

}

handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

getTimeSlot():Observable<any>{
return this.http.get(this.getTSurl).pipe(catchError(this.handleError));
}

updateOrAddTimeSlot(obj):Observable<any>{
    return this.http.post(this.updateOrAddurl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

    deleteTimeSlot(obj):Observable<any>{
        return this.http.post(this.deleteUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
        }
    
}