import { Component,Input, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialog, MatSnackBar } from '@angular/material';
import { NearestVendorComponent } from '../nearest-vendor/nearest-vendor.component';
import { orderGetService } from '../order-data/services/orderService';

@Component({
  selector: 'admin-delivery-data',
  templateUrl: './delivery-data.component.html',
  styleUrls: ['./delivery-data.component.css'],
  providers:[orderGetService]
})
export class DeliveryDataComponent implements OnInit {
   @Input() set Delivery(val){
     console.log(val);
this.deliverydata=val;
this.getAddress();
   }
   @Output() allocateChange = new EventEmitter();
   ;
public address=null;

   public deliverydata=null;
  constructor(private dialog:MatDialog,private orderService:orderGetService,private snackBar:MatSnackBar) { }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
  }
getAddress(){
  this.orderService.getSingleOrder({orderId:this.deliverydata.orderId}).subscribe(data=>{
    if(data.status=='S'){
this.address=data.address;
//console.log(data);
    }
  },err=>{
    if(err.message!=null){
      this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar(err,'');
    }
  })

}

  ngOnInit() {
  
  }
 
  allocVendor(){
    let dialogRef= this.dialog.open(NearestVendorComponent,{
      width:'60%',
     height:'70%',
     data:this.deliverydata,
     position:{
       top:'5%',
         left:"20%"
        }
        
    })
    dialogRef.afterClosed().subscribe(result=>{
      if(result!=null){
        this.allocateChange.emit({change:result})
      }
    })
  }

  
}
