import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { DeliveryOrderCrud } from '../moduleServices/orderDelivery';
import {time} from '../../../globalmodel/timeInterval';
@Component({
  selector: 'app-express-deliv-order',
  templateUrl: './express-deliv-order.component.html',
  styleUrls: ['./express-deliv-order.component.css'],
  providers:[DeliveryOrderCrud]
})
export class ExpressDelivOrderComponent implements OnInit,OnDestroy {
  @ViewChild('allocateComp') allocateComp;
  @ViewChild('unacceptComp') unacceptComp;
  public orderArray:Array<any>=null;
  public isAuto:Boolean=true;
  public intervalid;
  public selectedOrder:any=null;
  public errMessage=null;
  constructor(private delOrderCrud:DeliveryOrderCrud) { }

  backendCall(){
    this.delOrderCrud.getExpDelivOrders().subscribe(data=>{
      if(data.status=='S'){
      this.orderArray=data.doc;
      if(this.selectedOrder==null){
        this.selectedOrder=this.orderArray[0];
        this.errMessage=null;
      }
   
      }
    },err=>{
      this.orderArray=[];
      this.selectedOrder=null;
      if(err.message!=null){
this.errMessage=err.message;
      }else{
       this.errMessage=err;
      }
    })
  }
  checkchanges(data){
 if(data.change==true){
   this.backendCall();
   this.allocateComp.backendCall();
   this.unacceptComp.backendCall();
 }
  }



//   setAuto(){
// this.isAuto=true;

//   }

  ngOnInit() {
    this.backendCall();
this.intervalid=setInterval(()=>{
  this.backendCall();
},time.deliverytime)
  }

//   setManual(){
// this.isAuto=false;

//   }

  selectThis(obj){
    this.selectedOrder=obj;
  }

  takeUnaccepted(data){
    if(data!=null){
      this.selectedOrder=data;
      
    }
  }

  ngOnDestroy() {
    if (this.intervalid) {
      clearInterval(this.intervalid);
    }
  }
}
