import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { AdminService } from '../moduleServices/adminService';
import { Router,RouterEvent,NavigationEnd } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { RoleService } from '../moduleServices/roleService';
@Component({
  selector: 'app-admin-page',
  templateUrl: './admin-page.component.html',
  styleUrls: ['./admin-page.component.css'],
  providers:[AdminService,RoleService]
})
export class AdminPageComponent implements OnInit,OnDestroy {
public destroyed= new Subject();
public roles:Array<any>=[];
  constructor(public snackBar:MatSnackBar,public adminService:AdminService,
    public router:Router,public roleCrud:RoleService) { }
   
  public adminList:Array<any>=[];
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
  }

  getRoles(){
this.roleCrud.getAllRoles().subscribe(data=>{
  if(data.status=='S'){
this.roles=data.roles;
  }
},err=>{
  console.log(err);
  if(err.message){
this.openSnackBar(err.message,'');
  }else{
    this.openSnackBar(err,'');
  }
})
  }

  getAdminList(){
    this.adminService.getAllAdmins().subscribe(data=>{
      if(data.status=='S'){
this.adminList=data.admins;
      }
    },err=>{
      console.log(err);
      if(err.message){
this.openSnackBar(err.message,'');
      }else{
        this.openSnackBar(err,'');
      }
    })
  }

  ngOnInit() {
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
  this.getAdminList()
  this.getRoles();
    });
    this.getAdminList()
    this.getRoles();

  }

  ngOnDestroy(){
    this.destroyed.next();
    this.destroyed.complete();
    }

    createNewAdmin(){
      let obj={
        _id:null,
        name:null,
        id:null,
        activestatus:true
      }
      this.adminList.push(obj);
    }

}
