import {Injectable} from '@angular/core';
import { url} from '../../../globalmodel/url';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import {Observable,throwError} from 'rxjs';
import {catchError,retry} from 'rxjs/operators';
import { comments} from '../../../globalmodel/comments';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
@Injectable()

export class AdminService{
getMyRoleUrl=url.getAdminRoleUrl;
getAllAdminUrl=url.getAllAdminsUrl;
createAdminUrl=url.createAdminUrl;
updateAdminUrl=url.updateAdminUrl;
deleteAdminUrl=url.deleteAdminUrl;
deleteRoleofAdminUrl=url.deleteRoleofAdminMapUrl;
changeAdminUrl=url.changeAdminRoleUrl;
getAdminRightsUrl=url.getAdminRightsUrl;
constructor(private http:HttpClient){

}

handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };



getAllAdmins():Observable<any>{
return this.http.get(this.getAllAdminUrl,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
}

createAdmin(obj):Observable<any>{
  return this.http.post(this.createAdminUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  }

  updateAdmin(obj):Observable<any>{
    return this.http.post(this.updateAdminUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
    deleteAdmin(obj):Observable<any>{
      return this.http.post(this.deleteAdminUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
      }

changeAdminRoleMap(obj):Observable<any>{
    return this.http.post(this.changeAdminUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

getAdminRoleMap(obj):Observable<any>{
    return this.http.post(this.getMyRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

    getAdminRights():Observable<any>{
        return this.http.get(this.getAdminRightsUrl).pipe(catchError(this.handleError));
        }


}