import {Injectable} from '@angular/core';
import { url} from '../../../globalmodel/url';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import {Observable,throwError} from 'rxjs';
import {catchError,retry} from 'rxjs/operators';
import { comments} from '../../../globalmodel/comments';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
@Injectable()

export class RightService{

getAllRightsUrl=url.getAllRightsUrl;
createRightUrl=url.createRightUrl;
updateRightUrl=url.updateRightUrl;
deleteRightUrl=url.deleteRightUrl;
constructor(private http:HttpClient){

}

handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };



getAllRights():Observable<any>{
return this.http.get(this.getAllRightsUrl,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
}

createRight(obj):Observable<any>{
  return this.http.post(this.createRightUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  }

  updateRight(obj):Observable<any>{
    return this.http.post(this.updateRightUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
    deleteRight(obj):Observable<any>{
      return this.http.post(this.deleteRightUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
      }

 

}