
import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import {comments} from '../../../../globalmodel/comments';


@Injectable()
export class AllotedDeliveryService{
getAllotedDelivOrderUrl = url.getAllotedDelOrderurl;
shiftToreturnedSlotUrl = url.setToReturnedSloturl;
allocateDelBoyAutoUrl = url.autoAllocateDelBoy;
allocateDelBoyManUrl = url.manualAllocateDelBoy;
searchDelBoyUrl = url.searchDelBoyAccCord;
preAllocatedDelBoyUrl=url.getPreAllocatedDelBoy;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
    //   console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=0){
         err = error.error;
             }else{
                 err=comments.networkError;
             }
      
              return throwError(
          err);
             };

       
             getAllotedDelivOrder(object):Observable<any>{
                 let url =this.getAllotedDelivOrderUrl+"?type="+object.type;
        return this.http.get(url).pipe(catchError(this.handleError));
 
    }
shiftToReturnedSlot(object):Observable<any>{
    return this.http.post(this.shiftToreturnedSlotUrl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

}

autoDelBoyAllocation(object):Observable<any>{
    return this.http.post(this.allocateDelBoyAutoUrl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

}

manualDelBoyAllocation(object):Observable<any>{
    return this.http.post(this.allocateDelBoyManUrl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

}
searchDelBoy(id):Observable<any>{
    let url = this.searchDelBoyUrl+"?vendorId="+id;
    return this.http.get(url).pipe(catchError(this.handleError));

}

getPreAllocatedDelBoy(obj):Observable<any>{
    return this.http.post(this.preAllocatedDelBoyUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

}

}