import { Component, OnInit,Output, Input, EventEmitter } from '@angular/core';
import { DelBoyAccService } from './services/delBoyCrud';
@Component({
  selector: 'app-del-boy-account',
  templateUrl: './del-boy-account.component.html',
  styleUrls: ['./del-boy-account.component.css'],
  providers:[DelBoyAccService]
})
export class DelBoyAccountComponent implements OnInit {
  @Input()delBoy=null;
private date:Date;
  @Output() refresh= new EventEmitter();
  constructor(private delBoycrud:DelBoyAccService) { }

  ngOnInit() {
    this.date=new Date(this.delBoy.date);
  }

  

  setPay(val){
    //async calll  
this.delBoycrud.setDelBoyPay({empId:this.delBoy.empId,
  deliveryId:this.delBoy.deliveryId,type:this.delBoy.type,date:this.delBoy.date,paid:val}).subscribe(data=>{
  if(data.status=='S' && data.updated==true){
    this.refresh.emit({change:true});
  }else{
    alert('Error');
  }
},err=>{

if(err.message){
  alert(err.message);
}else{
  alert(err);
}
})
   

  }

  pay(){
this.setPay(true);

  }

  backTrack(){
    this.setPay(false);
  }


}
