import { Component, OnInit, OnDestroy } from '@angular/core';
import { RightService } from '../moduleServices/rightsService';
import {RightModel} from '../modulemodels/rights.model';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { MatSnackBar } from '@angular/material';
@Component({
  selector: 'app-right-crud',
  templateUrl: './right-crud.component.html',
  styleUrls: ['./right-crud.component.css'],
  providers:[RightService]
})
export class RightCrudComponent implements OnInit,OnDestroy {
public rights:Array<any>=[];
public destroyed = new Subject<any>();
  constructor(private rightCrud:RightService,private router:Router,private snackBar:MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });

  }
getRights(){
  this.rightCrud.getAllRights().subscribe(data=>{
    if(data.status=='S'){
    this.rights=data.rights;
    }else{
this.openSnackBar('Unknown Error','');
    }
  },err=>{
    if(err.message){
this.openSnackBar(err.message,'');
    }else{
this.openSnackBar(err,'');
    }
  })
}

  ngOnInit() {
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      takeUntil(this.destroyed)
    ).subscribe(() => {
      this.getRights();
    });
    this.getRights();

  
  }
  createNewRight(){
    let obj:RightModel=new RightModel();
    obj._id=null;
    obj.active=true;
   obj.rightName=null;
   obj.rightUri=null;
   obj.rightId=null;

    this.rights.push(obj);
  }

  ngOnDestroy(){
    this.destroyed.next();
    this.destroyed.complete();
  
  }

}
