import {Injectable} from '@angular/core';
import { url} from '../../../globalmodel/url';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import {Observable,throwError} from 'rxjs';
import {catchError,retry} from 'rxjs/operators';
import { comments} from '../../../globalmodel/comments';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
@Injectable()

export class RoleService{

getAllRolesUrl=url.getAllRolesUrl;
createRoleUrl=url.createRoleUrl;
updateRoleUrl=url.updateRoleUrl;
deleteRoleUrl=url.deleteRoleUrl;
getRightsofRoleUrl=url.getRightofRolesUrl;
addRightofRoleUrl=url.addRoleRightMapUrl;
deleteRightofRoleUrl=url.deleteRoleRightMapUrl;
constructor(private http:HttpClient){

}

handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };



getAllRoles():Observable<any>{
return this.http.get(this.getAllRolesUrl,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
}

createRole(obj):Observable<any>{
  return this.http.post(this.createRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  }

  updateRole(obj):Observable<any>{
    return this.http.post(this.updateRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
    deleteRole(obj):Observable<any>{
      return this.http.post(this.deleteRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
      }

addRoleRightMap(obj):Observable<any>{
    return this.http.post(this.addRightofRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
deleteRoleRightMap(obj):Observable<any>{
    return this.http.post(this.deleteRightofRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
getRightofRole(obj):Observable<any>{
    return this.http.post(this.getRightsofRoleUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }
}