import {Component, Input, Output, EventEmitter, OnInit } from '@angular/core'
import {getImagesUrl} from './services/getImagesUrl'
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import{ImagedialogComponent} from './imagedialog/imagedialog.component'
import { VerifyEmailComponent } from '../Dialogs/verify-email/verify-email.component';
import { UnverifyemailComponent } from '../Dialogs/unverifyemail/unverifyemail.component';
@Component({
selector:'employeeData',
templateUrl:'employeeData.component.html',
styleUrls:['employeeData.component.css'],
providers:[getImagesUrl]
})

export class EmployeeDataComponent implements OnInit{
@Input()user;
@Input()index;
@Output() verification = new EventEmitter<any>();
@Output() unverification = new EventEmitter<any>();
@Output() rejection = new EventEmitter<any>();
private docarray:Array<any>=[];
private keyArray:string[];
constructor(public dialog: MatDialog,private getImagesUrl:getImagesUrl){

}
ngOnInit(){
// this.getImageUrls();
if(this.user.ImageUrls!=null){
this.keyArray= Object.keys(this.user.ImageUrls).filter((key)=>{
    return key!='_id'
});
}
this.docarray=Object.keys(this.user.documents).filter(key=>{
    if(this.user.documents[key]!=null && key!='_id'){
        return key;
    }
})
}

// getImageUrls(){
//     let obj={'mobile_no':this.user.mobile_no,'keyArray':this.keyArray};
//  this.getImagesUrl.getImagesUrl(obj).subscribe(data=>{
//      if(data!=null){
//      this.imagesUrl=data.Images;
    
//   } },
//   err=>{
//       console.log(err);
//   })
// }

verify(user){
   // this.user.isVerified=true;
 let dialogRef=  this.dialog.open(VerifyEmailComponent,{
       data:user,
       width:'30%'
   })
   dialogRef.afterClosed().subscribe(res=>{
       if(res!=null && res.confirm==true){
         //  console.log(res);
         user.data={};
         user.data.subject="Verification of "+user.name+"justmyneed.com account";
         user.action='v';
        this.verification.emit(user);
       }
   })

}
unverify(user){
    //this.user.isVerified=false;
    let dialogRef=this.dialog.open(UnverifyemailComponent,{
        data:{user:user,action:'uv'},
        width:'45%'
    })

    dialogRef.afterClosed().subscribe(res=>{
        if(res!=null && res.confirm==true){
        //  console.log(res);
        user.data={};
        user.data=res.data;
        user.action='uv';
       this.unverification.emit(user);
        }
    })
 

}

reject(user){
    let dialogRef=this.dialog.open(UnverifyemailComponent,{
        data:{user:user,action:'rj'},
        width:'45%'
    })

    dialogRef.afterClosed().subscribe(res=>{
        if(res!=null && res.confirm==true){
            console.log(res);
            user.data={};
            user.data=res.data;
            user.action='rj';
            this.rejection.emit(user);
        }
    })
 

}

openimagedialog(image)
{
    const dialogRef = this.dialog.open(ImagedialogComponent, {
        width: '500px',
        data: image
      });
}
} 