import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { DeliveryOrderCrud } from '../moduleServices/orderDelivery';
import {DateAdapter, MatSnackBar} from '@angular/material';
import{DateFormat}from '../../../globalmodel/dateFormat';
import {time} from '../../../globalmodel/timeInterval';
@Component({
  selector: 'admin-standard-deliv-order',
  templateUrl: './standard-deliv-order.component.html',
  styleUrls: ['./standard-deliv-order.component.css'],
  providers:[DeliveryOrderCrud,{provide: DateAdapter, useClass: DateFormat}]
})
export class StandardDelivOrderComponent implements OnInit,OnDestroy {
@ViewChild('allocateComp') allocateComp;
@ViewChild('unacceptComp') unacceptComp;
  public orderArray:Array<any>=null;
public selectedOrder:any=null;
public isAuto:Boolean=true;
public intervalid;
public errMessage=null;
  constructor(private delOrderCrud:DeliveryOrderCrud,  private dateAdapter:DateAdapter<Date>,
    private snackBar:MatSnackBar) {
    dateAdapter.setLocale('en-in');
   }
   openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }


  backendCall(){
    this.delOrderCrud.getStdDelivOrders().subscribe(data=>{
      if(data.status=='S'){
      this.orderArray=data.doc;
      if(this.selectedOrder==null){
        this.selectedOrder=this.orderArray[0];
        this.errMessage=null;
      }
      }
    },err=>{
this.orderArray=[];
this.selectedOrder=null;
      if(err.message!=null){
this.errMessage=err.message;
      }else{
  this.errMessage=err;
      }
    })
  }


  ngOnInit() {
    this.backendCall();
    this.intervalid=setInterval(()=>{
      this.backendCall();
    },time.deliverytime)
 
  }

  checkchanges(data){
    if(data.change==true){
      this.backendCall();
      this.allocateComp.backendCall();
      this.unacceptComp.backendCall();
    }
     }
   

     selectThis(obj){
      this.selectedOrder=obj;
    }
  
    takeUnaccepted(data){
      if(data!=null){
        this.selectedOrder=data;
      }
    }
//   setAuto(){
// this.isAuto=true;

//   }

//   setManual(){
// this.isAuto=false

//   }
ngOnDestroy() {
  if (this.intervalid) {
    clearInterval(this.intervalid);
  }
}
}
