import { Component, OnInit,Output, Input, EventEmitter } from '@angular/core';
import { VendorAccService } from './services/vendorCrud';

@Component({
  selector: 'app-vendor-account',
  templateUrl: './vendor-account.component.html',
  styleUrls: ['./vendor-account.component.css'],
  providers:[VendorAccService]
})
export class VendorAccountComponent implements OnInit {
@Input()vendor=null;
@Output() refresh= new EventEmitter();
private date:Date;
  constructor(private vendorCrud:VendorAccService) { }
  ngOnInit() {
   this.date=new Date(this.vendor.date);

  }

  setPay(val){
    //async calll
  
this.vendorCrud.setVendorPay({empId:this.vendor.empId,deliveryId:this.vendor.deliveryId,
  type:this.vendor.type,date:this.vendor.date,paid:val}).subscribe(data=>{
  if(data.status=='S' && data.updated==true){
    this.refresh.emit({change:true});
  }else{
    alert('Error');
  }
},err=>{

if(err.message){
  alert(err.message);
}else{
  alert(err);
}
})
   
  }

  pay(){
this.setPay(true);

  }

  backTrack(){
    this.setPay(false);
  }

}
