import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { comments } from 'src/app/globalmodel/comments';


@Injectable()
export class VendorAccService{

setPayurl=url.setPayStatusUrl;

    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
      // console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=0){
         err = error.error;
             }else{
                err=comments.networkError;
             }
      
              return throwError(
          err);
             };

            setVendorPay(obj):Observable<any>{
                    return this.http.post(this.setPayurl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError))
             }

           

            }