import { Component, OnInit, Input } from '@angular/core';
import { getErrorMessage } from 'src/app/globalServices/Validation';
import { MatSnackBar, MatDialog } from '@angular/material';
import { RightService } from '../moduleServices/rightsService';
import {Router} from '@angular/router'
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OverrideComponent } from '../Dialogs/override/override.component';
@Component({
  selector: 'rights',
  templateUrl: './rights.component.html',
  styleUrls: ['./rights.component.css'],
  providers:[RightService]
})
export class RightsComponent implements OnInit {
@Input() right;
public error_messages;
public rightForm:FormGroup;
  constructor(public errorMessage:getErrorMessage,public snackBar:MatSnackBar,
    public rightCrud:RightService,public router:Router,public fb:FormBuilder,public dialog:MatDialog) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });

  }


  ngOnInit(){
    this.error_messages=this.errorMessage.geterror();
    this.rightForm=this.fb.group({
      rightName:[this.right.rightName,[Validators.required]],
      rightUri:[this.right.rightUri,[Validators.required]],
      active:[this.right.active,[Validators.required]]
    })

  }

  create(form){
  
    this.rightCrud.createRight(form).subscribe(data=>{
      if(data.status=='S'){
        if(data.result.isCreated==true){
          this.router.navigate([this.router.url]);
        }else if(data.result.override==true){
          let dialogRef=this.dialog.open(OverrideComponent,{
            width:'25%'
          })

          dialogRef.afterClosed().subscribe(result=>{
            if(result==true){
              this.update(form,data.result.doc);
            }
          })
        }
       
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })

  }

  update(form,right){

 form._id=right._id;
 form.rightId=right.rightId;
    this.rightCrud.updateRight(form).subscribe(data=>{
      if(data.status=='S' && data.result.isUpdated==true){
        this.router.navigate([this.router.url]);
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
this.openSnackBar(err.message,'')
      }else{
        this.openSnackBar(err,'');
      }
    })

  }

  delete(){

    this.rightCrud.deleteRight(this.right).subscribe(data=>{
      if(data.status=='S' && data.result.isDeleted==true){
        this.router.navigate([this.router.url]);
      }else{
        this.openSnackBar('Unknown Error','')
      }
    },err=>{
      if(err.message){
        this.openSnackBar(err.message,'')
              }else{
                this.openSnackBar(err,'');
              }
    })

  }


 
}
