import {Injectable} from '@angular/core';
import { url } from '../../../../globalmodel/url';
import { Observable ,throwError} from 'rxjs';
import {HttpClient,HttpErrorResponse} from '@angular/common/http'
import { catchError } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { comments } from 'src/app/globalmodel/comments';


@Injectable()

export class VoucherService{
getVouchersUrl=url.getVouchers;
getTemplatesUrl= url.gettemplateData;
pushVoucherUrl=url.pushVoucher;
constructor(private http:HttpClient){

}

handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

getVouchers():Observable<any>{
return this.http.get(this.getVouchersUrl).pipe(catchError(this.handleError));
}

pushVoucher(voucherobj):Observable<any>{
    return this.http.post(this.pushVoucherUrl,voucherobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

    getTemplateData():Observable<any>{
        return this.http.get(this.getTemplatesUrl).pipe(catchError(this.handleError));
        }
        
    
}