import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray, NgForm } from '@angular/forms';
import { DelivLevel, Level } from 'src/app/globalmodel/delivLevel';
import { getErrorMessage } from 'src/app/globalServices/Validation';
import { delOptDataService } from '../deliv-opt-page/services/delivStatusCrud';
import { MatSnackBar, MatDialog } from '@angular/material';
import { OverrideComponent } from '../Dialogs/override/override.component';
import { Router } from '@angular/router';

@Component({
  selector: 'admin-deliv-level',
  templateUrl: './deliv-level.component.html',
  styleUrls: ['./deliv-level.component.css']
})
export class DelivLevelComponent implements OnInit {
  @Input() delivLevel:DelivLevel=null;
  public error_messages:any;
  public levelArray:Array<FormGroup>=[];
public dialogRef:any;
public levelForm:FormGroup;
  constructor(private fb:FormBuilder,private errorMessage:getErrorMessage,private router:Router,
    private dellvlCrud:delOptDataService, private snackBar:MatSnackBar,private dialog:MatDialog) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
  }

createLvl(level:Level){
  if(level!=null){
  return this.fb.group({
    levelIndex:[level.LvlIndex,[Validators.required]],
    levelMessage:[level.LvlMsg,[Validators.required]]
  })
}else{
  return this.fb.group({
    levelIndex:[null,[Validators.required]],
    levelMessage:[null,[Validators.required]]
  })
}
}

  initializelevels=()=>{
    for(let level of this.delivLevel.Level){
     this.levelArray.push(this.createLvl(level));
    }
   
  }
  ngOnInit() {
    console.log(this.delivLevel);
    this.initializelevels();
    this.error_messages=this.errorMessage.geterror();
    this.levelForm=this.fb.group({
      maxLevel:[this.delivLevel.MaxLvl,[Validators.required]],
      levels:this.fb.array(this.levelArray)
    })

}

  addLevel(){
   
    (<FormArray>this.levelForm.controls.levels).push(this.createLvl(null))
  }

  remove(){
    if(this.delivLevel.LvlId!=null){
      this.dellvlCrud.remove({delivlevobj:this.delivLevel}).subscribe(data=>{
if(data.status=='S' && data.isDeleted==true){
  this.openSnackBar('Successfully Deleted','');
this.router.navigate([this.router.url]);
}else{
  this.openSnackBar('Error Occured','');
}
      },err=>{
        if(err.message!=null){
        this.openSnackBar(err.message,'');}
        else{
          this.openSnackBar(err,'');
        }
      })
    }
  }


  deleteLevel(index){
    (<FormArray>this.levelForm.controls.levels).removeAt(index);
  }
backendCall(form){
  if(this.delivLevel.LvlId==null){
    this.dellvlCrud.addDelivOptData({delivlevobj:form}).subscribe(data=>{
 if(data.status=='S' && data.isPushed==true){
   this.openSnackBar('Successfully Added','');
   this.router.navigate([this.router.url]);
 }else{
   this.openSnackBar('Error Occured','');
 }
    },err=>{
 if(err.status=='OVRDE'){
 this.dialogRef= this.dialog.open(OverrideComponent,{
 width:'362px',
 height:'200px'
 })
 this.dialogRef.afterClosed().subscribe(data=>{
   if(data==true){
   this.dellvlCrud.editDelivOptData({delivlevobj:form}).subscribe(data=>{
     if(data.status=='S' && data.isPushed==true){
       this.openSnackBar('Successfully Added','');
       this.router.navigate([this.router.url]);
     }else{
       this.openSnackBar('Error Occured','');
     }
   },err=>{
    if(err.message!=null){
    this.openSnackBar(err.message,'');}
    else{
      this.openSnackBar(err,'');
    }
  })}})
 }else{
  if(err.message!=null){
  this.openSnackBar(err.message,'');}
  else{
    this.openSnackBar(err,'');
  }
}})
  }else{
    this.dellvlCrud.editDelivOptData({delivlevobj:form}).subscribe(data=>{
      if(data.status=='S' && data.isPushed==true){
       this.openSnackBar('Successfully Added','');
       this.router.navigate([this.router.url]);
      }else{
       this.openSnackBar('Error Occured','');
      }
    },err=>{
      if(err.message!=null){
      this.openSnackBar(err.message,'');}
      else{
        this.openSnackBar(err,'');
      }
    })
  }
}


  onSubmit(form:NgForm){
  
 this.backendCall(form)
  }

}
