export class TimeSlot{
    _id:string;
    starthour:number;
    endhour:number;
    startmin:number;
    endmin:number;
    startampm:string;
    endampm:string;
    isAvail:Boolean;
    ststarthour:number;
    stendhour:number;

    constructor(){
        this._id=null;
        this.starthour=null;
        this.endhour=null;
        this.startmin=null;
        this.endmin=null;
        this.startampm=null;
        this.endampm=null;
        this.isAvail=true;
        this.ststarthour=null;
        this.stendhour=null;
    }
}