import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { comments } from 'src/app/globalmodel/comments';


@Injectable()
export class AccountService{

getUnpaidVendorsUrl=url.getUnpaidVendors;
getUnpaidDelBoysUrl=url.getUnpaidDelBoys;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
      // console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=0){
         err = error.error;
             }else{
                err=comments.networkError;
             }
      
              return throwError(
          err);
             };

             getUnpaidVendors(obj):Observable<any>{
                // let url=this.getUnpaidVendors+"?beginDate="+obj.beginDate+'&'+'endDate='+obj.endDate;
                return this.http.post(this.getUnpaidVendorsUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError))
             }

             getUnpaidDelBoys(obj):Observable<any>{
             //   let url=this.getUnpaidVendors+"?beginDate="+obj.beginDate+'&'+'endDate='+obj.endDate;
                return this.http.post(this.getUnpaidDelBoysUrl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError))
             }

            }