import { Component, OnInit,Input } from '@angular/core';
import { RightService } from '../moduleServices/rightsService';
import { Router } from '@angular/router';

@Component({
  selector: 'adminsidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css'],
  providers:[]
})
export class SidenavComponent implements OnInit {
@Input() set SetRights(val){

  if(val!=null && val.length!=0){
  this.rights=val;
  this.router.navigate([this.rights[0].rightUri]);
}
}

@Input() set SuperAdmin(val)
{
this.superAdmin=val;
}
public superAdmin:Boolean=false;
public rights:Array<any>=[];

  constructor(private router:Router) { }

  ngOnInit() {
  
  }

  goTo(right){
    localStorage.setItem('currentRT',right.rightId);
    this.router.navigate([right.rightUri]);
  }
   
  

}
