import { Component, OnInit,ViewChild } from '@angular/core';
import { AccountService } from './services/accountServices';
import {DateAdapter, MatSnackBar} from '@angular/material';
import{DateFormat}from '../../../globalmodel/dateFormat';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {Account} from '../../../globalmodel/account';
@Component({
  selector: 'app-account-crud',
  templateUrl: './account-crud.component.html',
  styleUrls: ['./account-crud.component.css'],
  providers:[AccountService,{provide: DateAdapter, useClass: DateFormat}]
})
export class AccountCrudComponent implements OnInit {
  public endDate:Date=new Date();
  public errvendorMess:string=null;
  public errDelBoyMess:string=null;
public beginDate:Date=new Date(this.endDate.getFullYear(),this.endDate.getMonth()-1,this.endDate.getDate());
public vendorList:MatTableDataSource<Account>;

public delBoyList:MatTableDataSource<Account>;


  constructor(public accountService:AccountService,public dateAdapter:DateAdapter<Date>,
    public snackBar:MatSnackBar) {
    dateAdapter.setLocale('en-in'); }

    openSnackBar(message: string, action: string) {
      this.snackBar.open(message, action, {
        duration: 2000,
      });
    }
    displayedColumns=['empId','date','deliveryId','cost'];

    applyVendorFilter(filterValue: string) {
      this.vendorList.filter = filterValue.trim().toLowerCase();
  
      if (this.vendorList.paginator) {
        this.vendorList.paginator.firstPage();
      }
    }

    applyDelBoyFilter(filterValue: string) {
      this.delBoyList.filter = filterValue.trim().toLowerCase();
  
      if (this.vendorList.paginator) {
        this.vendorList.paginator.firstPage();
      }
    }


getUnpaidVendors(){
  this.accountService.getUnpaidVendors({beginDate:this.beginDate,endDate:this.endDate}).subscribe(data=>{
    if(data.status=='S'){
     
  this.vendorList=new MatTableDataSource(data.accounts);
    }else{
     this.errvendorMess='Error';
    }
  },err=>{
    if(err.message){
      this.errvendorMess=err.message;
    }else{
this.errvendorMess=err;
    }
  })

}


getTotalCost(array) {
  return array.map(t => t.delprice).reduce((acc, value) => acc + value, 0);
}

getUnpaidDelBoys(){
  this.accountService.getUnpaidDelBoys({beginDate:this.beginDate,endDate:this.endDate}).subscribe(data=>{
    if(data.status=='S'){
//console.log(data);
this.delBoyList=new MatTableDataSource(data.accounts);
    }else{
      this.errDelBoyMess='Error';
     }
   },err=>{
     if(err.message){
       this.errDelBoyMess=err.message;
     }else{
 this.errDelBoyMess=err;
     }
   })
 

}

refresh(){
  this.getUnpaidVendors();
  this.getUnpaidDelBoys();
}

  ngOnInit() {
   this.getUnpaidVendors();
   this.getUnpaidDelBoys();

  }

  refreshVendor(data){
  if(data!=null && data.change==true){
    this.getUnpaidVendors();
  }
  }
  refreshDelBoy(data){
    if(data!=null && data.change==true){
      this.getUnpaidDelBoys();
    }
  }

}
